<template>
    <div>
        <router-link to="/" class="btn grey right">رجوع</router-link>
        <div class="container">
            <h4 class="center">متابعة المشروعات رقم: {{this.$route.params.main_job_id}}</h4>
            <h4 class="center">اسم المجموعة: {{this.name}}</h4>
        </div>
        <ul class="collection with-header">
            <li class="collection-header center"><h5>المشروعات الموجودة في هذه المجموعة</h5></li>
            <div v-for="main_job in main_jobs" v-bind:key="main_job.project_id">
                <li class="collection-item">
                    <div class="chip">{{main_job.project_id}}: {{main_job.project_desc}}</div>
                    <router-link class="secondary-content"
                        :to="{name: 'view-project', params: {main_job_id: main_job.main_job_id, project_id: main_job.project_id}}">
                        تفاصيل
                    </router-link>
                </li>
            </div>
        </ul>

        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-main-job', params: {main_job_id: this.$route.params.main_job_id}}" class="btn-floating btn-large red">
                تعديل
            </router-link>
            <router-link :to="{name: 'new-project', params: {main_job_id: this.$route.params.main_job_id}}" class="btn-floating btn-large red">
                اضافة
            </router-link>
        </div>
    </div>
</template>

<script>
import db from '../firebaseInit'
export default {
    name: 'view-main-job',
    data() {
        return {
            name,
            main_jobs: []
        }
    },
    mounted() {
        db.collection('main_jobs').where('main_job_id', '==', this.$route.params.main_job_id)
            .get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.name = doc.data().name
                })
            })
        db.collection('projects').where('main_job_id', '==', this.$route.params.main_job_id)
            .get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.main_jobs.push(doc.data())
                })
            })
    }
}
</script>


