<template>
    <div id="view-project">
        <div id="reactButtons">
            <router-link v-bind:to="{name: 'view-main-job', params: {main_job_id: this.$route.params.main_job_id}}" class="btn grey right">رجوع الى القائمة السابقة</router-link>
            <router-link to="/" class="btn grey">الذهاب الى متابعة المشروعات</router-link>
            <button v-if="role == 'admin'" @click="deleteProject" class="btn red">محو المشروع</button>
        </div>
        <ul class="collection with-header">
            <li v-if="project.project_id" class="collection-header"><h4>رقم المشروع: {{project.project_id}}</h4></li>
            <li v-if="project.project_desc" class="collection-header"><h4>المشروع: {{project.project_desc}}</h4></li>
            <li v-if="project.project_info" class="collection-header"><h4>بيانات المشروع: {{project.project_info}}</h4></li>
            <li v-if="project.residual" class="collection-header"><h4>المتبقي: {{project.residual}}</h4></li>
            <li v-if="project.to_start" class="collection-header"><h4>تاريخ البدء: {{project.to_start}}</h4></li>
            <li v-if="project.to_end" class="collection-header"><h4>تاريخ النهو: {{project.to_end}}</h4></li>
            <li v-if="project.project_id" class="collection-header"><h3>القطاعات <router-link id="sec" v-bind:to="{name: 'new-section', params: {main_job_id: this.$route.params.main_job_id, project_id: this.$route.params.project_id}}" class="right btn red">
                اضافة قطاع
            </router-link></h3></li>
        
            <div v-for="section in sections" v-bind:key="section.section_desc">
                <div v-if="section.main_job_id == project.main_job_id">
                    <li v-if="section.section_id" class="collection-header"><h3>القطاع رقم {{ section.section_id }}
                        <router-link v-bind:to="{name: 'edit-section', params: {section_id: section.section_id}}" class="btn right red">
                            تعديل بيانات القطاع
                        </router-link>
                        <button v-if="role == 'admin'" @click="deleteSection(section.section_id)" class="red btn">محو القطاع</button>
                    </h3></li>
                    <li v-if="section.section_desc" class="collection-header"><h4>الوصف: {{section.section_desc}}</h4></li>
                    <li v-if="section.contract_value" class="collection-header"><h4>قيمة التعاقد: {{section.contract_value}}</h4></li>
                    <li v-if="section.approved_20" class="collection-header"><h4>المعتمد حتى 30/6/2020: {{section.approved_20}}</h4></li>
                    <li v-if="section.approved_20_21" class="collection-header"><h4>المعتمد 2020/2021: {{section.approved_20_21}}</h4></li>
                    <li v-if="section.total_approved_21" class="collection-header"><h4>المعتمد حتى 30/6/2021: {{section.total_approved_21}}</h4></li>
                    <li v-if="section" class="collection-header"><h3>الشركات <router-link id="com" v-bind:to="{name: 'new-company', params: {section_id: section.section_id}}" class="right btn red">
                        اضافة شركة
                    </router-link></h3></li>
                    <div v-for="company in companies" v-bind:key="company.company_name && company.spent">
                        <div v-if="company.main_job_id == project.main_job_id & company.main_job_id == section.main_job_id">
                            <li v-if="company.company_id" class="collection-header">
                                <h3>الشركة رقم {{ company.company_id }}<router-link v-bind:to="{name: 'edit-company', 
                                    params: {section_id: section.section_id, 
                                    company_id: company.company_id}}" class="btn right red">
                                    تعديل بيانات الشركة
                                </router-link>
                                <button v-if="role == 'admin'" @click="deleteCompany(company.company_id, section.section_id)" class="btn red">محو الشركة</button>
                                </h3>
                            </li>
                            <li v-if="company.company_name" class="collection-header"><h4>الشركة المنفذة: {{company.company_name}}</h4></li>
                            <li v-if="company.spent" class="collection-header"><h4>المنصرف: {{company.spent}}</h4></li>
                            <li v-if="company.execution_rate" class="collection-header"><h4>نسبة التنفيذ الفعلي: {{company.execution_rate}}</h4></li>
                            <li v-if="company.contract_value" class="collection-header"><h4>قيمة التعاقد للشركة: {{company.contract_value}}</h4></li>
                        </div>
                    </div>
                </div>
            </div>
        </ul>
        <br>
        <br>

        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-project', params: {main_job_id: this.$route.params.main_job_id, project_id: this.$route.params.project_id}}" class="btn-floating btn-large red">
                تعديل
            </router-link>
        </div>
    </div>
</template>

<script>
import firebase from 'firebase'
import db from '../firebaseInit'
export default {
    name: 'view-project',
    data() {
        return {
            main_job_id: null,
            project_id: null,
            section_id: null,
            id: null,
            role: false,
            project: {},
            sections: [],
            companies: [],
            main_jobs: [],
            projects: [],
        }
    },
    mounted() {
        this.main_job_id = this.$route.params.main_job_id,
        this.project_id = this.$route.params.project_id,
        db.collection('main_jobs').orderBy('main_job_id').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                if (doc.data().main_job_id == this.$route.params.main_job_id) {
                    this.main_jobs.push(doc.data())
                }
            })
        })
        console.log(this.main_jobs)
        db.collection('projects').orderBy('project_id').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                this.projects.push(doc.data())
            })
        })
        db.collection('projects').where('project_id', '==', this.$route.params.project_id)
            .get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.project = doc.data()
                })
            })
        db.collection('sections').where('project_id', '==', this.$route.params.project_id)
            .get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.sections.push(doc.data())
                })
            })
        db.collection('companies').where('project_id', '==', this.$route.params.project_id)
            .get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.companies.push(doc.data())
                })
            })
        db.collection('users').doc(firebase.auth().currentUser.uid).get().then(snapshot => {
            this.role = snapshot.data().role
        })
    },
    methods: {
        deleteProject() {
            if (this.role == 'admin') {
                if(confirm('Are you sure?')) {
                    db.collection('projects').where('project_id', '==', this.$route.params.project_id).get()
                        .then(querySnapshot => {
                            querySnapshot.forEach(doc => {
                                if (doc.data().main_job_id == this.$route.params.main_job_id) {
                                    db.collection('sections').where('project_id', '==', doc.data().project_id).get()
                                        .then(querySnapshot => {
                                            querySnapshot.forEach(section => {
                                                section.ref.delete()
                                            })
                                        })
                                    db.collection('companies').where('project_id', '==', doc.data().project_id).get()
                                        .then(querySnapshot => {
                                            querySnapshot.forEach(company => {
                                                company.ref.delete()
                                            })
                                        })
                                    doc.ref.delete()
                                    this.$router.push('/')
                                }
                            })
                    })
                }
            } else {
                alert('An admin only can delete a project.')
            }
        },
        deleteSection(id) {
            if (this.role == 'admin') {
                if(confirm('Are you sure?')) {
                    db.collection('sections').where('section_id', '==', id).get()
                        .then(querySnapshot => {
                            querySnapshot.forEach(section => {
                                if ((section.data().main_job_id == this.$route.params.main_job_id) && (section.data().project_id == this.$route.params.project_id)){
                                    db.collection('companies').where('section_id', '==', section.data().section_id).get()
                                        .then(querySnapshot => {
                                            querySnapshot.forEach(company => {
                                                company.ref.delete()
                                            })
                                        })
                                    section.ref.delete()
                                    this.$router.push('/')
                                }
                            })
                        })
                }
            } else {
                alert('An admin only can delete a section.')
            }
        },
        deleteCompany(companyId, sectionId) {
            if (this.role == 'admin') {
                if (confirm('Are you sure?')) {
                    db.collection('companies').where('company_id', '==', companyId).get()
                        .then(querySnapshot => {
                            querySnapshot.forEach(company => {
                                if ((company.data().main_job_id == this.$route.params.main_job_id) &&
                                    (company.data().project_id == this.$route.params.project_id) &&
                                    (company.data().section_id == sectionId)) {
                                        company.ref.delete()
                                        this.$router.push('/')
                                    }
                            })
                        })
                }
            } else {
                alert('An admin only can delete a company.')
            }
        }
    }
}
</script>

<style scoped>
#reactButtons {
    padding-top: 1%;
}
#com {
    margin-top: 2%;
}
#sec {
    margin-top: 2%;
}


table{
      border-collapse: collapse;
      margin-top: 30px;
      border: 2px solid black;   
      font-family: sans-serif;
      text-align: right;   
      padding: 35px;
      width: 100%;    
}
#th1{
     background-color:firebrick;
     font-family: sans-serif;
     font-size: 24px;
     color: white;
     font-weight: bolder;
     text-align: center;
}

thead {

     background-color: #EEE;    
     font-weight: bolder;
     color: brown;
     text-align: right; 
}
 
td{
     border: 2px solid black;
     padding: 15px;
    
}
tfoot{
     font-weight: bolder;
     text-align: center;
     background-color: #EEE;    

    
}
</style>


